.hero .title {
  font-family: "Poppins", sans-serif;
  /* transform: rotate(-5deg); */
  font-size: 3.5rem;
  color: black;
  font-weight: 100;
  text-transform: capitalize;
}

.hero .subtitle {
  padding: 5px;
  color: black;
}

.hero .profession {
  padding-top: 0px;
  text-transform: uppercase;
  font-size: 2.5rem;
}
.hero .learner {
  padding-top: 6vh;
}
.full {
  padding-top: 5.25vh;
}
.hero.is-primary {
  margin: 0 0 2rem 0;
}
.is-mobile {
  display: none;
}

@media screen and (max-width: 768px) {
  .hero .profession {
    padding-top: 1px;
    text-transform: uppercase;
    font-size: 2.5rem;
  }
  
}

@media screen and (max-width: 575px) {
  .full {
    display: none;
  }
  .hero .learner {
    padding-top: 3vh;
    padding-bottom: 3vh;
  }

}
